/* Polices */
/* Selon le projet, il est souvent prÃ©fÃ©rable d'utiliser le lien @import des Google Fonts
 * plutÃ´t que de servir directement les polices de caractÃ¨res Ã  l'aide d'instructions @font-face
 * et d'un dossier de polices dans la structure de rÃ©pertoires.
 * La mÃ©thode ci-dessous vise Ã  offrir dans l'ordre de prioritÃ© les 3 formats possibles pour
 * assurer la meilleure compatibilitÃ©.
 */
@font-face {
    font-family: NewsCycle-bold;
    src:url(../polices/News_Cycle/NewsCycle-bold.woff2)format('woff2'),
    url(../polices/News_Cycle/NewsCycle-bold.woff) format('woff'),
    url(../polices/News_Cycle/NewsCycle-bold.ttf) format('truetype');
}
@font-face {
    font-family: NewsCycle;
    src:url(../polices/News_Cycle/NewsCycle-Regular.woff2)format('woff2'),
    url(../polices/News_Cycle/NewsCycle-Regular.woff) format('woff'),
    url(../polices/News_Cycle/NewsCycle-Regular.ttf) format('truetype');
}
h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4 {
    font-family: NewsCycle-bold, sans-serif ;
}
body{
    font-family: NewsCycle, sans-serif ;
}


/* Base dÃ©cimale */
html{
    font-size: 62.5%;
}


body {
    /* 16px pour toutes les largeurs d'Ã©cran */
    font-size: 1.6rem;
    /* facteur multiplicateur de la taille de la police 1.375x16 = 22 */
    line-height: 1.375;
}
@media (min-width: 600px) {
    body {
        line-height: 1.5;
    }
}
@media (min-width: 800px) {
    body {
        line-height: 1.75;
    }
}


/* HiÃ©rarchie responsive : plus proche = plus petit  &  plus loin = plus grand */
h1,
.h1 { /* = 32px */
    font-size: 3.2rem;
}
@media (min-width: 600px) {
    h1,
    .h1 { /* = 40px */
        font-size: 4rem;
    }
}
@media (min-width: 800px) {
    h1,
    .h1 { /* = 48px */
        font-size: 4.8rem;
    }
}

h2,
.h2 { /* = 24px */
    font-size: 2.4rem;
}
@media (min-width: 600px) {
    h2,
    .h2 { /* = 32px */
        font-size: 3.2rem;
    }
}
@media (min-width: 800px) {
    h2,
    .h2 { /* = 36px */
        font-size: 3.6rem;
    }
}

h3,
.h3 { /* = 18px */
    font-size: 1.8rem;
}
@media (min-width: 600px) {
    h3,
    .h3 {  /* = 24px */
        font-size: 2.4rem;
    }
}
@media (min-width: 800px) {
    h3,
    .h3 { /* = 30px */
        font-size: 3rem;
    }
}

h4,
.h4 { /* = 16px */
    font-size: 1.6rem;
}
@media (min-width: 600px) {
    h4,
    .h4 { /* = 18px */
        font-size: 1.8rem;
    }
}

@media (min-width: 800px) {
    h4,
    .h4 { /* = 22px */
        font-size: 2.2rem;
    }
}